@include font('Barlow Regular', 'barlow-regular-webfont');
@include font('Barlow Semibold', 'barlow-semibold-webfont');
@include font('Barlow Bold', 'barlow-bold-webfont');
@include font('Barlow Italic', 'barlow-italic-webfont');

@mixin brl-r {
	font-family: 'Barlow Regular', Arial, Helvetica, sans-serif; }
@mixin brl-sb {
	font-family: 'Barlow Semibold', Arial, Helvetica, sans-serif; }
@mixin brl-b {
	font-family: 'Barlow Bold', Arial, Helvetica, sans-serif; }
@mixin brl-it {
	font-family: 'Barlow Italic', Arial, Helvetica, sans-serif; }


$gray-common: #7c8092;
$violet-bright: #1b2550;
$violet-dim: #303342;
$aqua: #33cbcc;

$blue-bg: #032c9e;

body {
	background: #FFF;
	@include smooth-font;
	@include brl-r;
	line-height: 1.875;
	font-size: 16px;
	color: $gray-common; }



