body {
  background: #FFF;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Barlow Regular', Arial, Helvetica, sans-serif;
  line-height: 1.875;
  font-size: 16px;
  color: #7c8092;
}

.l {
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 20px;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 40px 50px;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  width: 100%;
  line-height: 1;
}

.header.is-fixed {
  background: rgba(0, 0, 0, 0.5);
}

.header__logo h1 {
  font-family: 'Barlow Bold', Arial, Helvetica, sans-serif;
  font-weight: normal;
  font-size: 24px;
}

.header__logo a {
  text-decoration: none;
  display: block;
  color: #fff;
}

.header__logo a span {
  display: inline-block;
  padding: 10px;
  background: #032c9e;
}

.header__links {
  display: flex;
  align-items: center;
}

.nav a {
  text-transform: uppercase;
  color: #fff;
  font-size: 16px;
  font-family: 'Barlow Bold', Arial, Helvetica, sans-serif;
  text-decoration: none;
  padding: 5px 25px;
  margin-left: 5px;
  position: relative;
  overflow: hidden;
}

.nav a:before {
  transform: scale3d(0, 1, 1);
  content: "";
  display: block;
  width: 100%;
  height: 2px;
  background: #fff;
  position: absolute;
  top: 15px;
  left: 0;
  transition: all 0.2s ease;
  transform-origin: 0 0;
}

.nav a:hover:before, .nav a.is-active:before {
  transform: scale3d(1, 1, 1);
}

.socials a {
  text-decoration: none;
  margin-left: 30px;
}

.socials a .icon {
  fill: #fff;
  font-size: 16px;
}

.hero {
  width: 100%;
  height: 121.53846vh;
  color: #fff;
  font-family: 'Barlow Bold', Arial, Helvetica, sans-serif;
  text-transform: uppercase;
  padding-top: 28.84615vh;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
}

.top-slider {
